// use fetch to get or post data.
// pass up useful errors to the calling function.
import store from '../store';

export default function fetchAPI({ url, data, method = 'GET', useAuth = false }) {
	let headers = {};

	if (useAuth) {
		headers.Authorization = `Token ${store.getState().auth.user.token}`;
	}

	return fetch(url, { headers, 'method': method, 'body': data })
		.then(response => {
			console.log('response ', response);
			if (response.ok) {
				const contentType = response.headers.get('Content-Type') || '';

				if (contentType.includes('application/json')) {
					return response.json().catch(error => {
						return Promise.reject(new Error('Invalid JSON: ' + error.message));
					});
				}

				if (contentType.includes('text/html')) {
					return response.text().then(html => {
						return {
							'page_type': 'generic',
							'html': html
						};
					}).catch(error => {
						return Promise.reject(new Error('HTML error: ' + error.message));
					});
				}

				return Promise.reject(new Error('Invalid content type: ' + contentType));
			}

			if (response.status === 404) {
				return Promise.reject(new Error('Page not found: ' + url));
			}

			if (response.status === 500) {
				return Promise.reject(new Error('Internal server error: ' + url));
			}

			return response.json().then(res => {
				// if the response is ok but the server rejected the request, e.g. because of a wrong password, we want to display the reason
				// the information is contained in the json()
				// there may be more than one error
				let errors = [];
				Object.keys(res).forEach((key) => {
					errors.push(`${key}: ${res[key]}`);
				});
				return Promise.reject(new Error(errors)
				);
			});
		}).catch(error => {
			console.log('not OK ', error);
			console.log('status ', error.status);
			console.log('statusText ', error.statusText);
			return Promise.reject(new Error(error.message));
		});
};
